import React, { Fragment, useState, useEffect } from "react";
import { MetaData } from "./layout/MetaData";
import Pagination from "react-js-pagination";
import { useDispatch, useSelector } from "react-redux";
import { getProducts } from "../actions/ProductActions";
import { Product } from "./Product/Product";
import { Loader } from "./layout/Loader";
import Swal from "sweetalert2";
import Slider from "rc-slider";
import "rc-slider/assets/index.css";

const { createSliderWithTooltip } = Slider;
const Range = createSliderWithTooltip(Slider.Range);

export const Home = ({ match }) => {
  const [currentPage, setCurrentPage] = useState(1);
  const [price, setPrice] = useState([1, 1000]);
  const [category, setCategory] = useState(null);
  const [rating, setRating] = useState(0);

  const categories = [
    "Electronics",
    "Cameras",
    "Laptops",
    "Accessories",
    "Headphones",
    "Food",
    "Books",
    "Clothes/Shoes",
    "Beauty/Health",
    "Sports",
    "Outdoor",
    "Home",
  ];
  const { loading, products, error, productsCount, resPerPage, filteredProductsCount } = useSelector(state => state.products)


  const dispatch = useDispatch();

  const keyword = match.params.keyword;

  useEffect(() => {
    if (error) {
      return Swal.fire({
        title: "Error!",
        text: "Do you want to continue",
        icon: "error",
        showConfirmButton: false,
        timer: 2000,
      });
    }
    dispatch(getProducts(keyword, currentPage, price, category, rating));
  }, [dispatch, error, currentPage, keyword, price, category, rating]);

  function setCurrentPageNo(pageNumber) {
    setCurrentPage(pageNumber);
  }
  let count = products?.productsCount;
  if (keyword) {
    count = filteredProductsCount;
  }

  return (
    <Fragment>
      <MetaData title={"Buy Best Products Online"} />
      {loading ? <Loader /> : <h1 id="products_heading">Latest Products</h1>}
      <div className="">
        <section id="products" className="container mt-5">
          <div className="row">
            {keyword ? (
              <Fragment>
                <div className="col-6 col-md-3 mt-5 mb-5">
                  <div className="px-5">
                    <Range
                      marks={{
                        1: `$1`,
                        1000: `$1000`,
                      }}
                      min={1}
                      max={1000}
                      defaultValue={[1, 1000]}
                      tipFormatter={(value) => `$${value}`}
                      tipProps={{
                        placement: "top",
                        visible: true,
                      }}
                      value={price}
                      onChange={(price) => setPrice(price)}
                    />
                    <hr className="my-5"></hr>
                    <div className="mt-5">
                      <h4 className="mb-3">categories</h4>
                      <ul className="pl-0">
                        {categories.map((category) => (
                          <li
                            style={{ cursor: "pointer", listStyleType: "none" }}
                            key={category}
                            onClick={() => setCategory(category)}
                          >
                            {category}
                          </li>
                        ))}
                      </ul>
                    </div>
                  </div>

                  <hr className="my-3"></hr>
                  <div className="mt-5">
                    <h4 className="mb-3">Ratings</h4>
                    <ul className="pl-0">
                      {[5, 4, 3, 2, 1].map((star) => (
                        <li
                          style={{ cursor: "pointer", listStyleType: "none" }}
                          key={star}
                          onClick={() => setRating(star)}
                        >
                          <div className="rating-outer">
                            <div
                              className="rating-inner"
                              style={{ width: `${star * 20}%` }}
                            ></div>
                          </div>
                        </li>
                      ))}
                    </ul>
                  </div>
                </div>
                <div className="col-6 col-md-9">
                  <div className="row">
                    {products?.products?.map((product) => (
                      <Product key={product._id} product={product} col={4} />
                    ))}
                  </div>
                </div>
              </Fragment>
            ) : (
              products?.products?.map((product) => (
                <Product key={product._id} product={product} col={3} />
              ))
            )}
          </div>
        </section>

        {products.resPerPage <= count && (
          <div className="d-flex justify-content-center mt-5">
            <Pagination
              activePage={currentPage}
              itemsCountPerPage={products.resPerPage}
              totalItemsCount={products.productsCount}
              onChange={setCurrentPageNo}
              nextPageText={"Next"}
              prevPageText={"Prev"}
              firstPageText={"First"}
              lastPageText={"Last"}
              itemClass="page-item"
              linkClass="page-link"
            />
          </div>
        )}
      </div>
    </Fragment>
  );
};
